import React, {Component} from "react";


class Sort extends Component {
  constructor(props){
    super(props);
    this.state = {

    };

    this.handleSort = this.handleSort.bind(this);
  }

  handleSort(orderBy, orderDir){

    console.log(orderBy + " - " + orderDir);
    this.props.onClickSort(orderBy, orderDir);
  }




render(){
  let {orderBy, orderDir} = this.props;

  let strSort = orderBy + " - " + orderDir;

 

  return (
    <div className="col-xs-3 col-sm-3 col-md-3 col-lg-3">
    <div className="dropdown">
      <button className="btn btn-default dropdown-toggle" type="button" id="dropdownMenu1" data-toggle="dropdown" aria-haspopup="true" aria-expanded="true">
        Sort by <span className="caret" />
      </button>
      <ul className="dropdown-menu" aria-labelledby="dropdownMenu1">
        <li> <button  onClick={()=> this.handleSort('name', 'asc')}>Name - ASC</button> </li>
        <li> <button  onClick={()=> this.handleSort('name', 'desc')}>Name - DESC</button> </li>
        <li role="separator" className="divider" />
        <li> <button  onClick={()=> this.handleSort('level', 'asc')}>Level - ASC</button> </li>
        <li> <button  onClick={()=> this.handleSort('level', 'desc')}>Level - DESC</button> </li>
      </ul>
      <span className="label label-success label-medium">{strSort}</span>
    </div>
  </div>
  );
}

 
}

export default Sort;