import React, {Component} from "react";
import Control from "./components/Control.js";
import List from "./components/List.js";
import Form from './components/Form.js';
import Title from './components/Title.js';

import {filter, includes, orderBy as funcOrderBy, remove} from "lodash";

const { v4: uuidv4 } = require('uuid');


class  App extends Component {
  constructor(props){
    super(props);
    this.state = {
        items: [],
        isShowForm: false,
        strSearch:'',
       orderBy: 'name',
       orderDir: 'asc',
       itemSelected: null



    };

    
    this.handleToggleForm = this.handleToggleForm.bind(this);
    this.closeForm = this.closeForm.bind(this);
    this.handleSearch = this.handleSearch.bind(this);
    this.handleSort = this.handleSort.bind(this);
    this.handleDelete = this.handleDelete.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleEdit = this.handleEdit.bind(this);
  }

  componentDidMount(){
    let items = JSON.parse(localStorage.getItem('task')) || [];
    this.setState({
      items : items,

    })

  }




  handleToggleForm(){
    this.setState({
      isShowForm: !this.state.isShowForm,
      itemSelected: null
    });
  }

  closeForm(){
    this.setState({
      isShowForm: false
    });
  }

  handleSearch(value){
    this.setState({
      strSearch: value
    });

  }

  handleSort(orderBy, orderDir){

    this.setState({
      orderBy: orderBy,
      orderDir: orderDir

    });


  }
  

handleSubmit(item){
  
  let {items} = this.state;

      if(item.id !== ''){

          items.forEach((elm, key)=> {
              if(elm.id === item.id){
                  items[key].name = item.name;
                  items[key].level = +item.level;
              }
          })

      } else {
        items.push({
          id : uuidv4(),
          name: item.name,
          level: +item.level,
        })
      }
      
      
      
      
    
    
    this.setState({
      item: items,
      isShowForm: false
    });

localStorage.setItem('task', JSON.stringify(items));
    
}



handleDelete(id){

console.log(id);
  let items = this.state.items;
  remove(items, (item)=>{
  return item.id === id;
  
});
this.setState({
items:items

});
localStorage.setItem('task', JSON.stringify(items));

}



handleEdit(item){
  this.setState({
    itemSelected: item,
    isShowForm: true

  })


}


render(){
  
  let elmForm     = null;
  let itemsOrigin = (this.state.items !== null) ? [...this.state.items] : [];
  let items       = []; 
  let {orderBy, orderDir, isShowForm, strSearch, itemSelected} = this.state;


items = filter(itemsOrigin, (item) => {
  return  includes(item.name.toLowerCase(), strSearch.toLowerCase());
});


items = funcOrderBy(items, [orderBy], [orderDir]);



//if(search.length > 0){
  //itemsOrigin.forEach((item) => {
  
    //if(item.name.toLowerCase().indexOf(search) !== -1){
      //items.push(item);
    //}

  //});

//}else{
  //items = itemsOrigin;
//}






  if(isShowForm){
      elmForm = <Form 

                  itemSelected={itemSelected}
                  onClickSubmit={this.handleSubmit}
                  onClickCancel={this.closeForm}/>;
  }

  return (
    <div className="row">
     
     <div>
       <Title />

        <Control 

        orderBy={orderBy}
        orderDir={orderDir}
        onClickSort={this.handleSort}
        onClickSearchGo={this.handleSearch} 
        onClickAdd={this.handleToggleForm} 
        isShowForm={isShowForm}/>
        {elmForm}


        <List 

        onClickEdit={this.handleEdit}
        onClickDelete={this.handleDelete}
        items={items} />
      </div>
    </div>


  );
}

 
}

export default App;
